/* stylelint-disable prettier/prettier */
/* stylelint-disable prettier/prettier */
/* stylelint-disable comment-empty-line-before */
/* stylelint-disable comment-whitespace-inside */

* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #f3ecec;
}

@font-face {
  font-family: StratosSkyeng;
  src: url("/statosskyeng-v1.001/Desktop\ fonts/stratosskyeng-regular.otf")
    format("opentype");
}

body {
  display: flex;
  flex-direction: column;
  padding: 158px 272px 157px 272px;
  justify-content: center;
  align-items: center;
  background: var(--blue-120, #004980);
}



.game {
  min-width: 480px;
  min-height: 459px;
  row-gap: 40px;
  border-radius: 12px;
  background: #c2f5ff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  margin-top: 52px;
  color: #004980;
  text-align: center;
  font-size: 40px;
  font-family: StratosSkyeng;
  font-style: normal;
  font-weight: 400;
  line-height: 48px;
}

.levels {
  display: flex;
  flex-direction: row;
  margin-left: 70px;
  margin-right: 70px;
  gap: 26px;
}

.chosen {
  border: 3px solid #0080c1;
  box-sizing: border-box;
}

.level {
  display: flex;
  width: 97px;
  height: 98px;
  flex-direction: column;
  justify-content: center;
  flex-shrink: 0;
  border-radius: 12px;
  background: white;
  color: var(--blue-100, #0080c1);
  text-align: center;
  font-size: 64px;
  font-family: StratosSkyeng;
  font-style: normal;
  font-weight: 400;
  line-height: 72px;
}

.level:hover {
  box-shadow: 0px 5px 10px rgba(23, 63, 143, 0.5);
}

.level-input {
  appearance: none;
}

.start-button {
  width: 246px;
  height: 50px;
  color: #fff;
  background-color: #7ac100;
  border: none;
  border-radius: 16px;
  box-shadow: 0 5px 15px rgb(0 0 0 / 40%);
  font-family: stratosskyeng-semilight;
  font-weight: 100px;
  font-size: 24px;
  margin-bottom: 64px;
}

.top {
  display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
}

.deck {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.timer-counting {
  font-weight: 400;
  font-size: 64px;
  color: #fff;
}

.restart {
  width: 246px;
  height: 50px;
  font-size: 24px;
  line-height: 32px;
  color: #FFFFFF;
  background-color: #7AC100;
  box-sizing: border-box;
  background-color: #7AC100;
  border-radius: 12px;
  border: none;
}

.cards {
  display: grid;
  justify-content: space-around;
  grid-template-columns: repeat(9, 1fr);
  gap: 16px;
}

.game-table {
  max-width: 900px;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
}

.game-card {
  width: 95px;
  height: 133px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  border-radius: 5px;
  transition: all 0.2s linear;
  margin: 16px;
}

.game-card img {
  display: none;
}

.game-card img + img {
  display: block;
}

.game-card.flip {
  transform: perspective(100px);
}

.game-card.flip img {
  display: block;
}

.game-card.flip img + img {
  display: none;
}

.successfully {
  border: 4px solid #94e00f;
}


